'use client'

import { useEffect, useState } from 'react'
import {
  Card,
  CardContent,
  CardHeader,
  CardTitle,
  CardDescription,
  CardFooter,
} from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select'
import {
  MapPin,
  Users,
  Package,
  TrendingUp,
  Filter,
  Search,
  ChevronRight,
  Home,
  Scale,
  User,
  Clock,
  Eye,
} from 'lucide-react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Progress } from '@/components/ui/progress'
import { Skeleton } from '@/components/ui/skeleton'

/* ================= TYPES ================= */

type Farmer = {
  id: number
  first_name: string
  last_name: string
}

type Field = {
  id: number
  field_name: string
  location: string
  total_area: number
  cultivatable_area: number
  ownership_type_display: string
  farmer_name: string
  crops_grown?: string[]
  last_visited?: string
}

type Stats = {
  total_fields: number
  total_area: number
  total_area_acres: number
  cultivatable_area_acres: number
  total_farmers: number
}

/* ================= COMPONENT ================= */

export function FarmsList() {
  const [fields, setFields] = useState<Field[]>([])
  const [farmers, setFarmers] = useState<Farmer[]>([])
  const [stats, setStats] = useState<Stats>({
    total_fields: 0,
    total_area: 0,
    total_area_acres: 0,
    cultivatable_area_acres: 0,
    total_farmers: 0,
  })
  const [loading, setLoading] = useState(true)
  const [searchQuery, setSearchQuery] = useState('')
  const [selectedFarmer, setSelectedFarmer] = useState('all')

  const token =
    typeof window !== 'undefined'
      ? localStorage.getItem('authToken')
      : null

  /* ================= FETCH FIELDS (ALL PAGES) ================= */

  const fetchAllFields = async () => {
    setLoading(true)
    let url: string | null = 'https://rda.ngrok.app/api/fields/'
    let allFields: Field[] = []

    try {
      while (url) {
        const res:any = await fetch(url.replace('http://', 'https://'), {
          headers: {
            Authorization: `Bearer ${token}`,
            'ngrok-skip-browser-warning': 'true',
          },
        })

        const data:any = await res.json()
        allFields.push(...(data.data || []))
        url = data.next ? data.next.replace('http://', 'https://') : null
      }

      setFields(allFields)
    } catch (err) {
      console.error('Error fetching fields:', err)
      setFields([])
    } finally {
      setLoading(false)
    }
  }

  /* ================= FETCH STATS ================= */

  const fetchStats = async () => {
    try {
      const res = await fetch(
        'https://rda.ngrok.app/api/dashboard/stats/',
        {
          headers: {
            Authorization: `Bearer ${token}`,
            
          },
        }
      )
   
      const data = await res.json()
      console.log('Fetched stats:', data)
      setStats(data)
    } catch (err) {
      console.error('Error fetching stats:', err)
    }
  }

  /* ================= FETCH FARMERS ================= */

  const fetchFarmers = async () => {
    try {
      const res = await fetch(
        'https://rda.ngrok.app/api/subadmin/assigned-farmers/',
        {
          headers: {
            Authorization: `Bearer ${token}`,
            'ngrok-skip-browser-warning': 'true',
          },
        }
      )
      const data = await res.json()
      console.log('Fetched farmers:', data)
      setFarmers(data.assigned_farmers || [])
    } catch (err) {
      console.error('Error fetching farmers:', err)
    }
  }

  /* ================= INITIAL LOAD ================= */

  useEffect(() => {
    if (!token) return
    fetchAllFields()
    fetchStats()
    fetchFarmers()
  }, [token])

  /* ================= FILTER ================= */

  const filteredFields = fields.filter(
    (f) =>
      f.field_name.toLowerCase().includes(searchQuery.toLowerCase()) ||
      f.location.toLowerCase().includes(searchQuery.toLowerCase()) ||
      f.farmer_name.toLowerCase().includes(searchQuery.toLowerCase())
  )

  /* ================= UI ================= */

  return (
    <div className="space-y-8">

      {/* HEADER */}
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold">Farms Management</h1>
          <p className="text-muted-foreground">
            Data loaded directly from API
          </p>
        </div>
        <Button variant="outline">
          <Package className="h-4 w-4 mr-2" />
          Export
        </Button>
      </div>

      {/* STATS FROM API */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
      
 <Card>
          <CardContent className="pt-6">
            <p className="text-sm">Total Farmers</p>
            <h3 className="text-2xl font-bold">
              {stats.total_farmers}
            </h3>
          </CardContent>
        </Card>
          <Card>
          <CardContent className="pt-6">
            <p className="text-sm">Total Farms</p>
            <h3 className="text-2xl font-bold">{stats.total_fields}</h3>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="pt-6">
            <p className="text-sm">Total Area</p>
            <h3 className="text-2xl font-bold">
              {stats.total_area} acres
            </h3>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="pt-6">
            <p className="text-sm">Cultivable Area</p>
            <h3 className="text-2xl font-bold">
              {stats.cultivatable_area_acres} acres
            </h3>
            <Progress value={stats.cultivatable_area_acres} className="mt-2" />
          </CardContent>
        </Card>

       
      </div>

      {/* SEARCH */}
      <Input
        placeholder="Search farms..."
        value={searchQuery}
        onChange={(e) => setSearchQuery(e.target.value)}
      />

      {/* LIST */}
      {loading ? (
        <Skeleton className="h-40" />
      ) : (
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          {filteredFields.map((field) => (
            <Card key={field.id}>
              <CardHeader>
                <CardTitle>{field.field_name}</CardTitle>
                <CardDescription>
                  <MapPin className="inline h-4 w-4 mr-1" />
                  {field.location}
                </CardDescription>
              </CardHeader>

              <CardContent>
                <p><strong>Farmer:</strong> {field.farmer_name}</p>
                <p><strong>Total Area:</strong> {field.total_area} acres</p>
                <p><strong>Cultivable:</strong> {field.cultivatable_area} acres</p>
              </CardContent>

              <CardFooter className="flex justify-end">
                <Button size="sm" variant="ghost">
                  <Eye className="h-4 w-4 mr-1" />
                  View
                </Button>
              </CardFooter>
            </Card>
          ))}
        </div>
      )}
    </div>
  )
}
